import { Component, OnInit } from '@angular/core';
import { AuthService } from'../services/auth.service';


@Component({
  selector: 'app-manage-site',
  templateUrl: './manage-site.component.html',
  styleUrls: ['./manage-site.component.css']
})
export class ManageSiteComponent implements OnInit {

  videofile : any;
  testmotional : String;
  constructor(private authService : AuthService) {

  }

  ngOnInit() {

  }

  onChangeSite() {
    
    let uploadData = {
      emailorphone: this.authService.getLoggedInUser().email,
      video : this.videofile,
      testmotional : this.testmotional
    }

    this.authService.uploadVideo(uploadData).subscribe(data => {
      if(data.success) {
        alert("Successfully Uploaded");
      } else {
        alert("Try Again");
      }
    })
  }

  onChange(event) {
    var files = event.srcElement.files;
    this.videofile = files[0];
    console.log(this.videofile);
  }
}



