import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import {RouterModule, Routes} from '@angular/router';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { NavbarComponent } from './navbar/navbar.component';
import {FlashMessagesModule,FlashMessagesService} from 'angular2-flash-messages';

// Service
import { AuthService } from './services/auth.service';
import { GetjsonService } from './services/getjson.service';
import { ValidateService } from './services/validate.service';

import { HomeComponent } from './home/home.component';
import { AboutComponent } from './about/about.component';
import { KchartComponent } from './kchart/kchart.component';
import { ManagerLoginComponent } from './manager-login/manager-login.component';
import { ManagerRegisterComponent } from './manager-register/manager-register.component';
import { ManageUserComponent } from './manage-user/manage-user.component';
import { ManageSiteComponent } from './manage-site/manage-site.component';

const appRoutes : Routes = [
  {path:'', component: ManageUserComponent},
  {path:'register', component: ManagerRegisterComponent},
  {path:'login', component: ManagerLoginComponent},
  {path:'about', component: AboutComponent},
  {path:'kchart', component: KchartComponent},
  {path:'manageuser', component: ManageUserComponent},
  {path:'managesite', component: ManageSiteComponent}
];

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    HomeComponent,
    AboutComponent,
    KchartComponent,
    ManagerLoginComponent,
    ManagerRegisterComponent,
    ManageUserComponent,
    ManageSiteComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(appRoutes),
    FlashMessagesModule
  ],
  providers: [
    AuthService,
    ValidateService,
    GetjsonService,
    FlashMessagesService
  ],
  bootstrap: [
    AppComponent
  ]
})
export class AppModule { }



